import JSZip from 'jszip';
import React, { useState, useEffect, useRef } from 'react';
import { extractChatData, detectBranches, exportChatAsMarkdown, saveTextFile } from './utils';
import MessageList from './MessageList';
import MessageDetail from './MessageDetail';
import ExportPanel from './ExportPanel';
import ToolActionPanel from './ToolActionPanel';
import MetadataPanel from './MetadataPanel';
import WelcomePage from './WelcomePage';
import UserSettingsPanel, { loadSettings, saveSettings } from './UserSettings';
import { Settings } from 'lucide-react';

const App = () => {
  // 
  const [files, setFiles] = useState([]);
  const [currentFileIndex, setCurrentFileIndex] = useState(0);
  const [processedData, setProcessedData] = useState(null);
  const [selectedMessageIndices, setSelectedMessageIndices] = useState([]);
  const [activeTab, setActiveTab] = useState('content');
  const [activeBottomPanelTab, setActiveBottomPanelTab] = useState('common');
  const [searchQuery, setSearchQuery] = useState('');
  const [contentSearchQuery, setContentSearchQuery] = useState('');
  const [statusMessage, setStatusMessage] = useState('');
  const [globalSearchMode, setGlobalSearchMode] = useState(false);
  // MapUUID
  const [fileMarkedItems, setFileMarkedItems] = useState(new Map());
  const [fileImportantItems, setFileImportantItems] = useState(new Map());
  const [fileDeletedItems, setFileDeletedItems] = useState(new Map());
  // UUIDMap
  const [markedItems, setMarkedItems] = useState(new Set());
  const [importantItems, setImportantItems] = useState(new Set());
  const [deletedItems, setDeletedItems] = useState(new Set());
  const [showCompleted, setShowCompleted] = useState(true);
  const [showDeleted, setShowDeleted] = useState(false);
  const [selectedBranchIndices, setSelectedBranchIndices] = useState([]);

  // 
  const [userSettings, setUserSettings] = useState(loadSettings());
  const [showSettingsPanel, setShowSettingsPanel] = useState(false);

  // 
  const [exportConfig, setExportConfig] = useState({
    includeThinking: true,
    includeTools: true,
    includeArtifacts: true,
    includeCitations: true,
    exportObsidianMetadata: true,
    hideTimestamps: false,
    exportMarkedOnly: false,
    obsidianProperties: [],
    obsidianTags: []
  });

  // Obsidian
  const [obsidianTags, setObsidianTags] = useState([]);
  const [obsidianProperties, setObsidianProperties] = useState([]);

  // /
  const [undoStack, setUndoStack] = useState([]);
  const [redoStack, setRedoStack] = useState([]);

  // 
  const [globalSearchResults, setGlobalSearchResults] = useState([]);

  // refs
  const fileInputRef = useRef(null);

  // 
  const handleSaveSettings = (newSettings) => {
    setUserSettings(newSettings);
    saveSettings(newSettings);
    setShowSettingsPanel(false);
    
    // 
    setShowCompleted(newSettings.behavior.showCompletedMessages);
    setShowDeleted(newSettings.behavior.showDeletedMessages);
    
    // 
    setExportConfig({
      ...exportConfig,
      includeThinking: newSettings.export.defaultIncludeThinking,
      includeTools: newSettings.export.defaultIncludeTools,
      includeArtifacts: newSettings.export.defaultIncludeArtifacts,
      includeCitations: newSettings.export.defaultIncludeCitations,
      exportObsidianMetadata: newSettings.export.defaultExportObsidianMetadata
    });
    
    // UI
    
    setStatusMessage('');
  };

  // 
  const handleLoadClick = () => {
    fileInputRef.current?.click();
  };

  // 
  const handleFileChange = async (event) => {
    const files = event.target.files;
    if (!files || files.length === 0) return;

    const fileList = Array.from(files).filter(file => file.name.endsWith('.json'));
    if (fileList.length === 0) {
      setStatusMessage('JSON');
      return;
    }

    setFiles(fileList);
    setCurrentFileIndex(0);
    setStatusMessage(` ${fileList.length} `);
  };

  // 
  const handleRemoveCurrentFile = () => {
    if (files.length === 0) return;

    const newFiles = [...files];
    newFiles.splice(currentFileIndex, 1);
    
    if (newFiles.length === 0) {
      setFiles([]);
      setProcessedData(null);
      setSelectedMessageIndices([]);
      setStatusMessage('');
      return;
    }
    
    const newIndex = currentFileIndex >= newFiles.length ? newFiles.length - 1 : currentFileIndex;
    setFiles(newFiles);
    setCurrentFileIndex(newIndex);
    setStatusMessage(` ${newFiles.length} `);
  };

  // 
  const loadMarkState = (fileUuid) => {
    if (!fileUuid) return;
    
    try {
      const savedState = localStorage.getItem(`chat_marks_${fileUuid}`);
      
      if (savedState) {
        const parsedState = JSON.parse(savedState);
        
        // 
        setMarkedItems(new Set(parsedState.markedItems || []));
        setImportantItems(new Set(parsedState.importantItems || []));
        setDeletedItems(new Set(parsedState.deletedItems || []));
        
        // Map
        const newFileMarkedItems = new Map(fileMarkedItems);
        newFileMarkedItems.set(fileUuid, parsedState.markedItems || []);
        setFileMarkedItems(newFileMarkedItems);
        
        const newFileImportantItems = new Map(fileImportantItems);
        newFileImportantItems.set(fileUuid, parsedState.importantItems || []);
        setFileImportantItems(newFileImportantItems);
        
        const newFileDeletedItems = new Map(fileDeletedItems);
        newFileDeletedItems.set(fileUuid, parsedState.deletedItems || []);
        setFileDeletedItems(newFileDeletedItems);
        
        setStatusMessage(` (${new Date(parsedState.timestamp).toLocaleString()})`);
        return true;
      }
    } catch (error) {
      console.error(":", error);
    }
    
    return false;
  };

  // 
  useEffect(() => {
    const processCurrentFile = async () => {
      if (!files || files.length === 0 || currentFileIndex >= files.length) {
        return;
      }

      const file = files[currentFileIndex];
      setStatusMessage(`: ${file.name}`);

      try {
        const text = await file.text();
        const jsonData = JSON.parse(text);
        let data = extractChatData(jsonData);
        data = detectBranches(data);
        setProcessedData(data);
        setSelectedMessageIndices([]);
        setSelectedBranchIndices([]);
        
        // 
        const fileUuid = data.meta_info.uuid;
        if (fileUuid) {
          // Map
          if (fileMarkedItems.has(fileUuid)) {
            setMarkedItems(new Set(fileMarkedItems.get(fileUuid)));
          } else {
            setMarkedItems(new Set());
            // localStorage
            loadMarkState(fileUuid);
          }
          
          if (fileImportantItems.has(fileUuid)) {
            setImportantItems(new Set(fileImportantItems.get(fileUuid)));
          } else {
            setImportantItems(new Set());
            // loadMarkState
          }
          
          if (fileDeletedItems.has(fileUuid)) {
            setDeletedItems(new Set(fileDeletedItems.get(fileUuid)));
          } else {
            setDeletedItems(new Set());
            // loadMarkState
          }
        } else {
          // UUID
          setMarkedItems(new Set());
          setImportantItems(new Set());
          setDeletedItems(new Set());
        }
        
        setStatusMessage(`: ${file.name}`);
      } catch (error) {
        console.error(":", error);
        setStatusMessage(`: ${error.message}`);
      }
    };

    processCurrentFile();
  }, [files, currentFileIndex, fileMarkedItems, fileImportantItems, fileDeletedItems]);

  // 
  const saveMarkState = (fileUuid, markedSet, importantSet, deletedSet) => {
    if (!fileUuid) return;
    
    const markState = {
      markedItems: Array.from(markedSet),
      importantItems: Array.from(importantSet),
      deletedItems: Array.from(deletedSet),
      timestamp: new Date().toISOString()
    };
    
    try {
      localStorage.setItem(
        `chat_marks_${fileUuid}`, 
        JSON.stringify(markState)
      );
    } catch (error) {
      console.error(":", error);
    }
  };

  // 
  const handlePrevFile = () => {
    if (currentFileIndex > 0) {
      setCurrentFileIndex(currentFileIndex - 1);
      // 
      setGlobalSearchMode(false);
    }
  };

  // 
  const handleNextFile = () => {
    if (currentFileIndex < files.length - 1) {
      setCurrentFileIndex(currentFileIndex + 1);
      // 
      setGlobalSearchMode(false);
    }
  };

  // 
  const handleMessageSelect = (index, isCtrlPressed) => {
    if (isCtrlPressed) {
      // Ctrl+: /
      setSelectedMessageIndices(prev => {
        const indexPos = prev.indexOf(index);
        if (indexPos === -1) {
          return [...prev, index].sort((a, b) => a - b);
        } else {
          return prev.filter(i => i !== index);
        }
      });
    } else {
      // : 
      setSelectedMessageIndices([index]);
    }
  };

  // 
  const saveStateForUndo = () => {
    const currentState = {
      markedItems: new Set(markedItems),
      importantItems: new Set(importantItems),
      deletedItems: new Set(deletedItems)
    };
    setUndoStack(prev => [...prev, currentState]);
    setRedoStack([]);
  };

  // 
  const handleUndo = () => {
    if (undoStack.length === 0 || !processedData || !processedData.meta_info.uuid) return;
    
    const fileUuid = processedData.meta_info.uuid;
    
    // 
    const currentState = {
      markedItems: new Set(markedItems),
      importantItems: new Set(importantItems),
      deletedItems: new Set(deletedItems)
    };
    setRedoStack(prev => [...prev, currentState]);
    
    // 
    const prevState = undoStack[undoStack.length - 1];
    setMarkedItems(prevState.markedItems);
    setImportantItems(prevState.importantItems);
    setDeletedItems(prevState.deletedItems);
    setUndoStack(prev => prev.slice(0, -1));
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(prevState.markedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    const newFileImportantItems = new Map(fileImportantItems);
    newFileImportantItems.set(fileUuid, Array.from(prevState.importantItems));
    setFileImportantItems(newFileImportantItems);
    
    const newFileDeletedItems = new Map(fileDeletedItems);
    newFileDeletedItems.set(fileUuid, Array.from(prevState.deletedItems));
    setFileDeletedItems(newFileDeletedItems);
    
    // 
    saveMarkState(fileUuid, prevState.markedItems, prevState.importantItems, prevState.deletedItems);
    
    setStatusMessage("");
  };

  // 
  const handleRedo = () => {
    if (redoStack.length === 0 || !processedData || !processedData.meta_info.uuid) return;
    
    const fileUuid = processedData.meta_info.uuid;
    
    // 
    const currentState = {
      markedItems: new Set(markedItems),
      importantItems: new Set(importantItems),
      deletedItems: new Set(deletedItems)
    };
    setUndoStack(prev => [...prev, currentState]);
    
    // 
    const nextState = redoStack[redoStack.length - 1];
    setMarkedItems(nextState.markedItems);
    setImportantItems(nextState.importantItems);
    setDeletedItems(nextState.deletedItems);
    setRedoStack(prev => prev.slice(0, -1));
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(nextState.markedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    const newFileImportantItems = new Map(fileImportantItems);
    newFileImportantItems.set(fileUuid, Array.from(nextState.importantItems));
    setFileImportantItems(newFileImportantItems);
    
    const newFileDeletedItems = new Map(fileDeletedItems);
    newFileDeletedItems.set(fileUuid, Array.from(nextState.deletedItems));
    setFileDeletedItems(newFileDeletedItems);
    
    // 
    saveMarkState(fileUuid, nextState.markedItems, nextState.importantItems, nextState.deletedItems);
    
    setStatusMessage("");
  };

  // 
  const handleToggleCompleted = (index) => {
    if (!processedData || !processedData.meta_info.uuid) return;
    
    saveStateForUndo();
    const fileUuid = processedData.meta_info.uuid;
    const newMarkedItems = new Set(markedItems);
    
    if (newMarkedItems.has(index)) {
      newMarkedItems.delete(index);
      setStatusMessage(``);
    } else {
      newMarkedItems.add(index);
      setStatusMessage(``);
    }
    
    setMarkedItems(newMarkedItems);
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(newMarkedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    // 
    saveMarkState(fileUuid, newMarkedItems, importantItems, deletedItems);
  };

  // 
  const handleMarkCompleted = () => {
    if (selectedMessageIndices.length === 0 || !processedData || !processedData.meta_info.uuid) return;

    saveStateForUndo();
    const fileUuid = processedData.meta_info.uuid;
    const newMarkedItems = new Set(markedItems);
    selectedMessageIndices.forEach(index => newMarkedItems.add(index));
    setMarkedItems(newMarkedItems);
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(newMarkedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    // 
    saveMarkState(fileUuid, newMarkedItems, importantItems, deletedItems);
    
    setStatusMessage(` ${selectedMessageIndices.length} `);
  };

  // 
  const handleMarkImportant = () => {
    if (selectedMessageIndices.length === 0 || !processedData || !processedData.meta_info.uuid) return;

    saveStateForUndo();
    const fileUuid = processedData.meta_info.uuid;
    const newImportantItems = new Set(importantItems);
    selectedMessageIndices.forEach(index => newImportantItems.add(index));
    setImportantItems(newImportantItems);
    
    // Map
    const newFileImportantItems = new Map(fileImportantItems);
    newFileImportantItems.set(fileUuid, Array.from(newImportantItems));
    setFileImportantItems(newFileImportantItems);
    
    // 
    saveMarkState(fileUuid, markedItems, newImportantItems, deletedItems);
    
    setStatusMessage(` ${selectedMessageIndices.length} `);
  };

  // 
  const handleMarkDeleted = () => {
    if (selectedMessageIndices.length === 0 || !processedData || !processedData.meta_info.uuid) return;

    saveStateForUndo();
    const fileUuid = processedData.meta_info.uuid;
    const newDeletedItems = new Set(deletedItems);
    selectedMessageIndices.forEach(index => newDeletedItems.add(index));
    setDeletedItems(newDeletedItems);
    
    // Map
    const newFileDeletedItems = new Map(fileDeletedItems);
    newFileDeletedItems.set(fileUuid, Array.from(newDeletedItems));
    setFileDeletedItems(newFileDeletedItems);
    
    // 
    saveMarkState(fileUuid, markedItems, importantItems, newDeletedItems);
    
    setStatusMessage(` ${selectedMessageIndices.length} `);
  };

  // 
  const handleDeleteBranch = (branchIndices) => {
    if (!processedData || !processedData.branches || branchIndices.length === 0) {
      return;
    }
    
    // 
    setStatusMessage(` ${branchIndices.length} `);
  };

  // 
  const handleSelectMultipleBranches = (indices) => {
    if (!processedData || !processedData.branches) return;
    
    setSelectedBranchIndices(indices);
    
    // 
    if (indices.length === 0) {
      setSelectedMessageIndices([]);
      return;
    }
    
    // 
    const allMessageIndices = new Set();
    indices.forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      branch.forEach(msg => allMessageIndices.add(msg.index));
    });
    
    // 
    setSelectedMessageIndices(Array.from(allMessageIndices));
  };

  // Markdown
  const handleExportMarkdown = () => {
    if (!processedData) {
      setStatusMessage('');
      return;
    }

    const config = {
      ...exportConfig,
      markedItems: new Set([...markedItems, ...importantItems]),
      obsidianTags,
      obsidianProperties
    };

    const markdown = exportChatAsMarkdown(processedData, config);
    const fileName = `${processedData.meta_info.title || 'chat'}__${new Date().toISOString().slice(0, 10)}.md`;

    saveTextFile(markdown, fileName);
    setStatusMessage(`: ${fileName}`);
  };
  
  // 
  const handleExportMarked = () => {
    if (!processedData) {
      setStatusMessage('');
      return;
    }

    const config = {
      ...exportConfig,
      exportMarkedOnly: true,
      markedItems: new Set([...markedItems, ...importantItems]),
      obsidianTags,
      obsidianProperties
    };

    const markdown = exportChatAsMarkdown(processedData, config);
    const fileName = `${processedData.meta_info.title || 'chat'}__${new Date().toISOString().slice(0, 10)}.md`;

    saveTextFile(markdown, fileName);
    setStatusMessage(`: ${fileName}`);
  };

  // 
  const handleExportAllMarked = async () => {
    if (!files || files.length === 0) {
      setStatusMessage('');
      return;
    }
    
    setStatusMessage('...');
    let successCount = 0;
    let timestamp = new Date().toISOString().slice(0, 10);
    
    // 
    const currentFileIdx = currentFileIndex;
    
    try {
      // zip
      const zip = new JSZip();
      
      // 
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        setStatusMessage(` ${i+1}/${files.length}: ${file.name}`);
        
        try {
          // 
          const text = await file.text();
          const jsonData = JSON.parse(text);
          let data = extractChatData(jsonData);
          data = detectBranches(data);
          
          // 
          const fileUuid = data.meta_info.uuid;
          if (!fileUuid) continue;
          
          let fileMarked = fileMarkedItems.get(fileUuid) || [];
          let fileImportant = fileImportantItems.get(fileUuid) || [];
          
          // localStorage
          if (fileMarked.length === 0 && fileImportant.length === 0) {
            try {
              const savedState = localStorage.getItem(`chat_marks_${fileUuid}`);
              if (savedState) {
                const parsedState = JSON.parse(savedState);
                fileMarked = parsedState.markedItems || [];
                fileImportant = parsedState.importantItems || [];
              }
            } catch (error) {
              console.error(":", error);
            }
          }
          
          // 
          if (fileMarked.length === 0 && fileImportant.length === 0) {
            continue;
          }
          
          // 
          const config = {
            ...exportConfig,
            exportMarkedOnly: true,
            markedItems: new Set([...fileMarked, ...fileImportant]),
            obsidianTags,
            obsidianProperties
          };
          
          // Markdown
          const markdown = exportChatAsMarkdown(data, config);
          
          // zip
          const fileName = `${data.meta_info.title || `chat_${i}`}_.md`;
          zip.file(fileName, markdown);
          
          successCount++;
        } catch (error) {
          console.error(` ${file.name} :`, error);
        }
      }
      
      // 
      if (successCount === 0) {
        setStatusMessage('');
        return;
      }
      
      // zip
      const zipContent = await zip.generateAsync({ type: 'blob' });
      
      // zip
      const zipFileName = `Claude__${timestamp}.zip`;
      const url = URL.createObjectURL(zipContent);
      const a = document.createElement('a');
      a.href = url;
      a.download = zipFileName;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      
      setStatusMessage(` ${successCount} `);
    } catch (error) {
      console.error(":", error);
      setStatusMessage(`: ${error.message}`);
    }
  };

  // 
  const handleExportSelected = () => {
    if (!processedData || selectedMessageIndices.length === 0) {
      setStatusMessage('');
      return;
    }

    // 
    const selectedMessages = selectedMessageIndices
      .map(index => processedData.chat_history.find(msg => msg.index === index))
      .filter(Boolean);

    if (selectedMessages.length === 0) return;

    const customData = {
      meta_info: { ...processedData.meta_info },
      chat_history: selectedMessages
    };

    customData.meta_info.title += ` - ${selectedMessages.length}`;

    const config = {
      ...exportConfig,
      markedItems: new Set(), // 
      obsidianTags,
      obsidianProperties
    };

    const markdown = exportChatAsMarkdown(customData, config);
    const fileName = `${processedData.meta_info.title || 'chat'}__${new Date().toISOString().slice(0, 10)}.md`;

    saveTextFile(markdown, fileName);
    setStatusMessage(` ${selectedMessages.length} `);
  };

  // 
  const handleExportAndMark = () => {
    handleExportMarkdown();
    
    if (!processedData || !processedData.meta_info.uuid) return;
    
    const fileUuid = processedData.meta_info.uuid;
    saveStateForUndo();
    const newMarkedItems = new Set(markedItems);
    processedData.chat_history.forEach(msg => {
      newMarkedItems.add(msg.index);
    });
    setMarkedItems(newMarkedItems);
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(newMarkedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    // 
    saveMarkState(fileUuid, newMarkedItems, importantItems, deletedItems);
    
    setStatusMessage('');
  };

  // 
  const handleMarkBranchCompleted = () => {
    if (selectedBranchIndices.length === 0 || !processedData || !processedData.branches || !processedData.meta_info.uuid) {
      setStatusMessage('');
      return;
    }
    
    const fileUuid = processedData.meta_info.uuid;
    saveStateForUndo();
    const newMarkedItems = new Set(markedItems);
    
    let messageCount = 0;
    selectedBranchIndices.forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      if (branch && branch.length > 0) {
        branch.forEach(msg => {
          newMarkedItems.add(msg.index);
          messageCount++;
        });
      }
    });
    
    if (messageCount === 0) {
      setStatusMessage('');
      return;
    }
    
    setMarkedItems(newMarkedItems);
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(newMarkedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    // 
    saveMarkState(fileUuid, newMarkedItems, importantItems, deletedItems);
    
    setStatusMessage(` ${selectedBranchIndices.length}  ${messageCount} `);
  };

  // 
  const handleExportSelectedBranch = () => {
    if (selectedBranchIndices.length === 0 || !processedData || !processedData.branches) {
      setStatusMessage('');
      return;
    }
    
    // 
    const selectedMessages = [];
    const messageIndices = new Set();
    
    selectedBranchIndices.forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      if (branch && branch.length > 0) {
        branch.forEach(msg => {
          if (!messageIndices.has(msg.index)) {
            messageIndices.add(msg.index);
            selectedMessages.push(msg);
          }
        });
      }
    });
    
    if (selectedMessages.length === 0) {
      setStatusMessage('');
      return;
    }
    
    const customData = {
      meta_info: { ...processedData.meta_info },
      chat_history: selectedMessages
    };
    
    const branchLabel = selectedBranchIndices.length === 1 
      ? ` ${selectedBranchIndices[0] + 1}` 
      : `${selectedBranchIndices.length}`;
    customData.meta_info.title += ` - ${branchLabel}`;
    
    const config = {
      ...exportConfig, 
      markedItems: new Set([...markedItems, ...importantItems]),
      obsidianTags,
      obsidianProperties
    };
    
    const markdown = exportChatAsMarkdown(customData, config);
    const fileName = `${processedData.meta_info.title || 'chat'}_${branchLabel}_${new Date().toISOString().slice(0, 10)}.md`;
    
    saveTextFile(markdown, fileName);
    setStatusMessage(` ${branchLabel} : ${fileName}`);
  };

  // 
  const performGlobalSearch = async () => {
    if (!searchQuery.trim() || !files || files.length === 0) return;
    
    setStatusMessage('...');
    setGlobalSearchMode(true);
    
    const query = searchQuery.toLowerCase();
    const results = [];
    
    // 
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      
      try {
        // 
        const text = await file.text();
        const jsonData = JSON.parse(text);
        let data = extractChatData(jsonData);
        
        // 
        const matches = data.chat_history.filter(msg => 
          msg.display_text.toLowerCase().includes(query)
        );
        
        if (matches.length > 0) {
          results.push({
            fileIndex: i,
            fileName: file.name,
            title: data.meta_info.title || 'Untitled',
            matchCount: matches.length,
            matches: matches.map(msg => ({
              messageIndex: msg.index,
              preview: msg.display_text.substring(0, 100),
              sender: msg.sender_label
            }))
          });
        }
      } catch (error) {
        console.error(` ${file.name} :`, error);
      }
    }
    
    setGlobalSearchResults(results);
    
    if (results.length === 0) {
      setStatusMessage(` "${searchQuery}" `);
    } else {
      const totalMatches = results.reduce((sum, file) => sum + file.matchCount, 0);
      setStatusMessage(` ${results.length}  ${totalMatches} `);
    }
  };

  // 
  const jumpToMessage = (fileIndex, messageIndex) => {
    if (fileIndex !== currentFileIndex) {
      setCurrentFileIndex(fileIndex);
      // 
      setTimeout(() => {
        setSelectedMessageIndices([messageIndex]);
        setContentSearchQuery(searchQuery);
        setTimeout(() => {
          window.find(searchQuery, false, false, true, false, true, false);
        }, 300);
      }, 500);
    } else {
      setSelectedMessageIndices([messageIndex]);
      setContentSearchQuery(searchQuery);
      setTimeout(() => {
        window.find(searchQuery, false, false, true, false, true, false);
      }, 300);
    }
  };

  // 
  const handleSearchMessages = (direction = 'next') => {
    if (!searchQuery.trim()) return;
    
    // 
    if (files.length > 1 && !globalSearchMode) {
      performGlobalSearch();
      return;
    }
    
    // 
    if (!processedData?.chat_history) return;
    
    const query = searchQuery.toLowerCase();
    const history = processedData.chat_history;
    const totalMessages = history.length;
    
    if (totalMessages === 0) return;
    
    let startIdx = 0;
    if (selectedMessageIndices.length > 0) {
      startIdx = selectedMessageIndices[selectedMessageIndices.length - 1];
      if (direction === 'next') {
        startIdx = (startIdx + 1) % totalMessages;
      } else {
        startIdx = (startIdx - 1 + totalMessages) % totalMessages;
      }
    }
    
    // 
    let currentIdx = startIdx;
    let found = false;
    let foundIndices = [];
    
    // 
    for (let i = 0; i < totalMessages; i++) {
      const msg = history[i];
      if (msg.display_text.toLowerCase().includes(query)) {
        foundIndices.push(i);
      }
    }
    
    if (foundIndices.length === 0) {
      setStatusMessage(` "${searchQuery}" `);
      return;
    }
    
    // 
    if (selectedMessageIndices.length === 0 || direction === 'next') {
      // 
      for (const idx of foundIndices) {
        if (idx > startIdx || (direction === 'next' && selectedMessageIndices.length === 0)) {
          currentIdx = idx;
          found = true;
          break;
        }
      }
      // 
      if (!found && foundIndices.length > 0) {
        currentIdx = foundIndices[0];
        found = true;
      }
    } else {
      // 
      for (let i = foundIndices.length - 1; i >= 0; i--) {
        if (foundIndices[i] < startIdx) {
          currentIdx = foundIndices[i];
          found = true;
          break;
        }
      }
      // 
      if (!found && foundIndices.length > 0) {
        currentIdx = foundIndices[foundIndices.length - 1];
        found = true;
      }
    }
    
    if (found) {
      setSelectedMessageIndices([currentIdx]);
      setStatusMessage(` "${searchQuery}"  (${foundIndices.indexOf(currentIdx) + 1}/${foundIndices.length})`);
      
      // 
      setContentSearchQuery(searchQuery);
      setTimeout(() => {
        window.find(searchQuery, false, false, true, false, true, false);
      }, 300);
    } else {
      setStatusMessage(` "${searchQuery}" `);
    }
  };

  // 
  const handleSearchInContent = (direction = 'next') => {
    if (!contentSearchQuery.trim()) return;
    
    // 
    window.find(contentSearchQuery, false, direction !== 'next', true, false, true, false);
  };

  // 
  const handleClearMarks = () => {
    if (selectedMessageIndices.length === 0 || !processedData || !processedData.meta_info.uuid) return;

    saveStateForUndo();
    const fileUuid = processedData.meta_info.uuid;
    const newMarkedItems = new Set(markedItems);
    const newImportantItems = new Set(importantItems);
    const newDeletedItems = new Set(deletedItems);
    
    selectedMessageIndices.forEach(index => {
      newMarkedItems.delete(index);
      newImportantItems.delete(index);
      newDeletedItems.delete(index);
    });
    
    setMarkedItems(newMarkedItems);
    setImportantItems(newImportantItems);
    setDeletedItems(newDeletedItems);
    
    // Map
    const newFileMarkedItems = new Map(fileMarkedItems);
    newFileMarkedItems.set(fileUuid, Array.from(newMarkedItems));
    setFileMarkedItems(newFileMarkedItems);
    
    const newFileImportantItems = new Map(fileImportantItems);
    newFileImportantItems.set(fileUuid, Array.from(newImportantItems));
    setFileImportantItems(newFileImportantItems);
    
    const newFileDeletedItems = new Map(fileDeletedItems);
    newFileDeletedItems.set(fileUuid, Array.from(newDeletedItems));
    setFileDeletedItems(newFileDeletedItems);
    
    // 
    saveMarkState(fileUuid, newMarkedItems, newImportantItems, newDeletedItems);
    
    setStatusMessage(` ${selectedMessageIndices.length} `);
  };

  // 
  const handleCopySelected = () => {
    if (selectedMessageIndices.length === 0 || !processedData) {
      return;
    }

    const selectedTexts = selectedMessageIndices
      .map(index => {
        const msg = processedData.chat_history.find(msg => msg.index === index);
        if (msg) {
          return `${msg.sender_label}\n${msg.display_text}\n`;
        }
        return null;
      })
      .filter(Boolean);

    if (selectedTexts.length === 0) return;

    navigator.clipboard.writeText(selectedTexts.join('\n'))
      .then(() => setStatusMessage(''))
      .catch(err => setStatusMessage(`: ${err.message}`));
  };

  // 
  const renderBottomPanel = () => {
    return (
      <div className="border-t border-gray-300">
        {/*  */}
        <div className="bg-gray-100 border-b border-gray-300 flex">
          <button 
            className={`px-4 py-2 text-sm font-medium tab-button ${
              activeBottomPanelTab === 'common' ? 'bg-blue-50 text-blue-700 border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50 border-r border-gray-300'
            }`}
            onClick={() => setActiveBottomPanelTab('common')}
          >
            
          </button>
          <button 
            className={`px-4 py-2 text-sm font-medium tab-button ${
              activeBottomPanelTab === 'export' ? 'bg-blue-50 text-blue-700 border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50 border-r border-gray-300'
            }`}
            onClick={() => setActiveBottomPanelTab('export')}
          >
            
          </button>
          <button 
            className={`px-4 py-2 text-sm font-medium tab-button ${
              activeBottomPanelTab === 'metadata' ? 'bg-blue-50 text-blue-700' : 'text-gray-600 hover:bg-gray-50'
            }`}
            onClick={() => setActiveBottomPanelTab('metadata')}
          >
            
          </button>
        </div>
        
        {/*  */}
        {activeBottomPanelTab === 'common' && (
          <ToolActionPanel 
            showCompleted={showCompleted}
            setShowCompleted={setShowCompleted}
            showDeleted={showDeleted}
            setShowDeleted={setShowDeleted}
            handleMarkCompleted={handleMarkCompleted}
            handleMarkImportant={handleMarkImportant}
            handleMarkDeleted={handleMarkDeleted}
            handleClearMarks={handleClearMarks}
            handleCopySelected={handleCopySelected}
            handleUndo={handleUndo}
            handleRedo={handleRedo}
            undoStack={undoStack}
            redoStack={redoStack}
            selectedMessageIndices={selectedMessageIndices}
          />
        )}
        
        {activeBottomPanelTab === 'export' && (
          <ExportPanel 
            exportConfig={exportConfig}
            setExportConfig={setExportConfig}
            handleExportMarkdown={handleExportMarkdown}
            handleExportAndMark={handleExportAndMark}
            handleExportMarked={handleExportMarked}
            handleExportAllMarked={handleExportAllMarked}
            handleExportSelected={handleExportSelected}
            selectedMessageIndices={selectedMessageIndices}
          />
        )}
        
        {activeBottomPanelTab === 'metadata' && (
          <MetadataPanel
            obsidianTags={obsidianTags}
            setObsidianTags={setObsidianTags}
            obsidianProperties={obsidianProperties}
            setObsidianProperties={setObsidianProperties}
            processedData={processedData}
          />
        )}
      </div>
    );
  };

  // 
  const renderGlobalSearchResults = () => {
    if (!globalSearchMode || globalSearchResults.length === 0) return null;
    
    return (
      <div className="fixed top-10 left-1/4 right-1/4 bg-white rounded-lg shadow-xl border border-gray-300 z-50 max-h-[80vh] overflow-auto">
        <div className="p-3 border-b border-gray-300 flex justify-between items-center bg-blue-50">
          <h3 className="font-bold text-blue-800">: "{searchQuery}"</h3>
          <button 
            className="text-gray-600 hover:text-gray-800"
            onClick={() => setGlobalSearchMode(false)}
          >
             
          </button>
        </div>
        
        <div className="p-3">
          {globalSearchResults.map((fileResult, idx) => (
            <div key={idx} className="mb-4 border-b border-gray-200 pb-2">
              <div className="font-medium text-blue-700 mb-2">
                {fileResult.title} ({fileResult.matchCount})
              </div>
              
              {fileResult.matches.map((match, matchIdx) => (
                <div 
                  key={matchIdx}
                  className="py-1 px-2 hover:bg-blue-50 cursor-pointer rounded mb-1"
                  onClick={() => jumpToMessage(fileResult.fileIndex, match.messageIndex)}
                >
                  <div className="text-sm">
                    <span className="font-medium">{match.sender}:</span> {match.preview}...
                  </div>
                </div>
              ))}
            </div>
          ))}
        </div>
      </div>
    );
  };

  // JSX
  return (
    <div className="App flex flex-col h-screen">
      {/*  */}
      <input 
        type="file" 
        ref={fileInputRef} 
        onChange={handleFileChange} 
        style={{ display: 'none' }} 
        multiple 
        accept=".json" 
      />
      
      {/*  */}
      <div className="bg-gray-800 text-white px-4 py-2 text-sm flex justify-between items-center">
        <div>{statusMessage}</div>
        <button
          className="text-white hover:text-blue-200 focus:outline-none"
          onClick={() => setShowSettingsPanel(true)}
          title=""
        >
          <Settings className="h-5 w-5" />
        </button>
      </div>
      
      {/*  */}
      {showSettingsPanel && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <UserSettingsPanel
            currentSettings={userSettings}
            onSave={handleSaveSettings}
            onCancel={() => setShowSettingsPanel(false)}
          />
        </div>
      )}
      
      {/*  */}
      {renderGlobalSearchResults()}
      
      {/*  */}
      {files.length === 0 ? (
        // 
        <WelcomePage handleLoadClick={handleLoadClick} />
      ) : (
        // 
        <div className="flex flex-1 overflow-hidden">
          {/*  */}
          <MessageList 
            processedData={processedData}
            selectedMessageIndices={selectedMessageIndices}
            handleMessageSelect={handleMessageSelect}
            handleToggleCompleted={handleToggleCompleted}
            files={files}
            currentFileIndex={currentFileIndex}
            handlePrevFile={handlePrevFile}
            handleNextFile={handleNextFile}
            handleRemoveCurrentFile={handleRemoveCurrentFile}
            searchQuery={searchQuery}
            setSearchQuery={setSearchQuery}
            handleSearchMessages={handleSearchMessages}
            markedItems={markedItems}
            importantItems={importantItems}
            deletedItems={deletedItems}
            showCompleted={showCompleted}
            showDeleted={showDeleted}
            selectedBranchIndices={selectedBranchIndices}
            handleSelectMultipleBranches={handleSelectMultipleBranches}
            handleMarkBranchCompleted={handleMarkBranchCompleted}
            handleExportSelectedBranch={handleExportSelectedBranch}
            handleDeleteBranch={handleDeleteBranch}
            handleLoadClick={handleLoadClick}
            handleMarkDeleted={handleMarkDeleted}
          />
          
          {/*  */}
          <div className="flex flex-col flex-1 content-panel">
            <MessageDetail 
              processedData={processedData}
              selectedMessageIndices={selectedMessageIndices}
              activeTab={activeTab}
              setActiveTab={setActiveTab}
              contentSearchQuery={contentSearchQuery}
              setContentSearchQuery={setContentSearchQuery}
              handleSearchInContent={handleSearchInContent}
            />
            
            {/*  */}
            {renderBottomPanel()}
          </div>
        </div>
      )}
    </div>
  );
};

export default App;