// BranchManager.js - 
// o()o

import React, { useState, useEffect } from 'react';
import { ChevronDown, ChevronRight, CheckSquare, FileText, Trash2, GitMerge } from 'lucide-react';

const BranchManager = ({
  processedData,
  selectedBranchIndices = [],
  handleSelectBranch,
  handleMarkBranchCompleted,
  handleExportSelectedBranch,
  handleDeleteBranch
}) => {
  const [expandedGroups, setExpandedGroups] = useState({
    branches: true,
    stats: false,
  });
  
  const [selectedBranches, setSelectedBranches] = useState(new Set(selectedBranchIndices));
  
  // selectedBranchIndices
  useEffect(() => {
    setSelectedBranches(new Set(selectedBranchIndices));
  }, [selectedBranchIndices]);
  
  // /
  const toggleExpanded = (group) => {
    setExpandedGroups(prev => ({
      ...prev,
      [group]: !prev[group]
    }));
  };
  
  // 
  const toggleBranchSelection = (index, isCtrlPressed) => {
    if (!isCtrlPressed) {
      // 
      const newSelection = new Set([index]);
      setSelectedBranches(newSelection);
      handleSelectBranch(Array.from(newSelection));
    } else {
      //  (Ctrl+)
      const newSelection = new Set(selectedBranches);
      if (newSelection.has(index)) {
        newSelection.delete(index);
      } else {
        newSelection.add(index);
      }
      setSelectedBranches(newSelection);
      handleSelectBranch(Array.from(newSelection));
    }
  };
  
  // /
  const toggleSelectAllBranches = () => {
    if (selectedBranches.size === (processedData?.branches?.length || 0)) {
      // 
      setSelectedBranches(new Set());
      handleSelectBranch([]);
    } else {
      // 
      const allBranchIndices = processedData?.branches?.map((_, idx) => idx) || [];
      setSelectedBranches(new Set(allBranchIndices));
      handleSelectBranch(allBranchIndices);
    }
  };
  
  // 
  const selectRelatedBranches = () => {
    if (selectedBranches.size === 0 || !processedData?.branches) return;
    
    // 
    const selectedMessages = new Set();
    Array.from(selectedBranches).forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      branch.forEach(msg => selectedMessages.add(msg.index));
    });
    
    // 
    const relatedBranchIndices = [];
    processedData.branches.forEach((branch, idx) => {
      const hasRelatedMessage = branch.some(msg => selectedMessages.has(msg.index));
      if (hasRelatedMessage) {
        relatedBranchIndices.push(idx);
      }
    });
    
    // 
    setSelectedBranches(new Set(relatedBranchIndices));
    handleSelectBranch(relatedBranchIndices);
  };
  
  // 
  const handleMergeBranches = () => {
    if (selectedBranches.size < 2 || !processedData?.branches) return;
    
    // 
    const mergedMessages = new Set();
    Array.from(selectedBranches).forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      branch.forEach(msg => mergedMessages.add(msg.index));
    });
    
    // 
    handleSelectBranch(Array.from(mergedMessages));
  };
  
  // 
  const calculateSelectedMessageCount = () => {
    if (selectedBranches.size === 0 || !processedData?.branches) return 0;
    
    // 
    const uniqueMessages = new Set();
    Array.from(selectedBranches).forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      branch.forEach(msg => uniqueMessages.add(msg.index));
    });
    
    return uniqueMessages.size;
  };
  
  return (
    <div className="border-t border-gray-300 p-2 bg-gray-50">
      {/*  */}
      <div 
        className="flex items-center justify-between cursor-pointer mb-2"
        onClick={() => toggleExpanded('branches')}
      >
        <div className="flex items-center font-medium text-gray-700">
          {expandedGroups.branches ? 
            <ChevronDown className="h-4 w-4 mr-1" /> : 
            <ChevronRight className="h-4 w-4 mr-1" />
          }
          <span></span>
          {selectedBranches.size > 0 && (
            <span className="ml-2 text-xs text-blue-600">
               {selectedBranches.size}  {calculateSelectedMessageCount()} 
            </span>
          )}
        </div>
      </div>
      
      {/*  */}
      {expandedGroups.branches && (
        <>
          {/*  */}
          <div className="mb-2 flex justify-between items-center">
            <div className="flex items-center text-xs text-gray-500">
              <input 
                type="checkbox" 
                id="select-all-branches" 
                checked={selectedBranches.size === (processedData?.branches?.length || 0) && processedData?.branches?.length > 0}
                onChange={toggleSelectAllBranches}
                className="mr-1" 
              />
              <label htmlFor="select-all-branches" className="mr-3"></label>
              
              <span className="mr-1">:</span>
              <kbd className="px-1 py-0.5 bg-gray-200 rounded text-xs mr-1">Ctrl</kbd>
              <span>+</span>
            </div>
            
            {selectedBranches.size > 0 && (
              <button 
                className="text-xs text-blue-600 hover:text-blue-800"
                onClick={selectRelatedBranches}
              >
                
              </button>
            )}
          </div>
          
          {/*  */}
          <div className="max-h-32 overflow-y-auto mb-2 border border-gray-200 bg-white rounded">
            <div 
              className={`p-1.5 border-b border-gray-200 text-sm cursor-pointer hover:bg-blue-50`}
              onClick={() => handleSelectBranch([])}
            >
              
            </div>
            {processedData?.branches?.map((branch, idx) => (
              <div 
                key={idx}
                className={`p-1.5 border-b border-gray-200 text-sm cursor-pointer hover:bg-blue-50 ${
                  selectedBranches.has(idx) ? 'bg-blue-100' : ''
                }`}
                onClick={(e) => toggleBranchSelection(idx, e.ctrlKey)}
              >
                <div className="flex items-center justify-between">
                  <span> {idx + 1} ({branch.length})</span>
                  
                  <input 
                    type="checkbox" 
                    checked={selectedBranches.has(idx)}
                    onChange={(e) => {
                      e.stopPropagation();
                      toggleBranchSelection(idx, true);
                    }}
                    onClick={(e) => e.stopPropagation()}
                    className="ml-2"
                  />
                </div>
              </div>
            ))}
          </div>
          
          {/*  */}
          <div className="flex flex-wrap gap-2">
            <button 
              className="bg-blue-100 text-blue-700 py-1 px-2 rounded text-xs flex items-center"
              onClick={handleMarkBranchCompleted}
              disabled={selectedBranches.size === 0}
            >
              <CheckSquare className="h-3 w-3 mr-1" />
              
            </button>
            
            <button 
              className="bg-green-100 text-green-700 py-1 px-2 rounded text-xs flex items-center"
              onClick={handleExportSelectedBranch}
              disabled={selectedBranches.size === 0}
            >
              <FileText className="h-3 w-3 mr-1" />
              
            </button>
            
            {selectedBranches.size > 1 && (
              <button 
                className="bg-purple-100 text-purple-700 py-1 px-2 rounded text-xs flex items-center"
                onClick={handleMergeBranches}
              >
                <GitMerge className="h-3 w-3 mr-1" />
                
              </button>
            )}
            
            <button 
              className="bg-red-100 text-red-700 py-1 px-2 rounded text-xs flex items-center"
              onClick={() => handleDeleteBranch(Array.from(selectedBranches))}
              disabled={selectedBranches.size === 0}
            >
              <Trash2 className="h-3 w-3 mr-1" />
              
            </button>
          </div>
        </>
      )}
      
      {/*  */}
      <div 
        className="flex items-center justify-between cursor-pointer mt-3 mb-1"
        onClick={() => toggleExpanded('stats')}
      >
        <div className="flex items-center font-medium text-gray-700">
          {expandedGroups.stats ? 
            <ChevronDown className="h-4 w-4 mr-1" /> : 
            <ChevronRight className="h-4 w-4 mr-1" />
          }
          <span></span>
        </div>
      </div>
      
      {expandedGroups.stats && processedData?.branches && (
        <div className="p-2 bg-white rounded border border-gray-200 text-sm">
          <div className="grid grid-cols-2 gap-2">
            <div>
              <div className="text-gray-700">:</div>
              <div className="font-medium">{processedData.branches.length}</div>
            </div>
            <div>
              <div className="text-gray-700">:</div>
              <div className="font-medium">
                {processedData.chat_history.filter(msg => msg.is_branch_point).length}
              </div>
            </div>
            <div>
              <div className="text-gray-700">:</div>
              <div className="font-medium">
                {processedData.branches.reduce((max, branch) => Math.max(max, branch.length), 0)}
              </div>
            </div>
            <div>
              <div className="text-gray-700">:</div>
              <div className="font-medium">
                {processedData.chat_history.reduce((max, msg) => Math.max(max, msg.branch_level || 0), 0)}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default BranchManager;