import React from 'react';
import { FileText, Heart } from 'lucide-react';

const WelcomePage = ({ handleLoadClick }) => {
  return (
    <div className="flex flex-col items-center justify-center h-full bg-gradient-to-b from-blue-50 to-white p-6 text-center">
      <div className="mb-6">
        <div className="text-5xl font-bold text-pink-500 mb-2">Claude</div>
        <div className="text-xl text-blue-600">v3.0 - Made with <Heart className="inline-block h-5 w-5 text-pink-500" fill="#ec4899" /> by Lyra</div>
      </div>
      
      <div className="bg-white p-6 rounded-lg shadow-md max-w-2xl mb-8">
        <h2 className="text-2xl font-bold text-blue-700 mb-4"></h2>
        <p className="text-gray-700 mb-4">
          Claude
        </p>
        
        <div className="text-left">
          <h3 className="text-lg font-semibold text-blue-600 mb-2"></h3>
          <ul className="list-disc list-inside mb-4 text-gray-700 space-y-1">
            <li>ClaudeJSON</li>
            <li></li>
            <li></li>
            <li></li>
            <li>MarkdownObsidian</li>
            <li></li>
          </ul>
        </div>
      </div>
      
      <button
        className="px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-500 text-white text-lg font-semibold rounded-full shadow-lg hover:from-blue-600 hover:to-purple-600 transition-all duration-300 flex items-center"
        onClick={handleLoadClick}
      >
        <FileText className="mr-2 h-5 w-5" />
        JSON
      </button>
      
      <div className="mt-6 text-sm text-gray-500">
        <p></p>
        <p className="mt-2 text-xs text-gray-400">P.S. </p>
      </div>
    </div>
  );
};

export default WelcomePage;